* {
  @include border-box;
}

html {
  height: 100%;
  max-height: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  background-color: $bg-grey;
  color: $base-color;
  font-family: $base-font-family;
  font-size: 100%;
  font-weight: 400;
  height: 100%;
  letter-spacing: 0.01rem;
  line-height: $line-height;
  max-height: 100%;
  color:#fff;
}

a {
  color: $celestial-blue;

  &:hover {
    color: $midnight-blue;
  }

}

img {
  max-width: 100%;
}

// Layout items for grid amendments

.iframecont {
  max-width:100vw;
  height:auto;
  padding:30px;
  // padding-top: 120px;
}
.IframeWrapper {
  border:1px solid #0f0;
  overflow:hidden;
  background-color:#444;
  border:10px solid #444;
  border-radius:10px;
  height:auto;
  -webkit-box-shadow: 0 0 5px 0 rgba(0,0,0,0.3);
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.3);
  color:#fff;
  iframe {
    -webkit-box-shadow: 0 0 5px 0 rgba(0,0,0,0.5);
    box-shadow: 0 0 5px 0 rgba(0,0,0,0.5);
    //border:2px solid #ccc;
    //margin:0 auto;
    margin-left:5px;
    margin-bottom:5px;
  }
}

.framedetails {
  position:relative;
  //padding:10px;
  // -right:60px;
  h3 {
    float:left;
    margin-top:0;
    margin-bottom:10px;
  }
  p {
    display:inline-block;
    // text-align:right;
    margin-bottom:10px;
  }
}

.controlBlock {
  // position:fixed;
  // z-index:99;
  // top:0;
  background-color:#444;
  width:100%;
  padding:20px;
  -webkit-box-shadow: 0 0 5px 0 rgba(0,0,0,0.3);
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.3);
  input {
    margin-right:20px;
  }
  .inputBlock {
    display:inline-block;
  }
  @include inverse-breakpoint(500px) {
    .AddInputs {
      float:none;
    }
  }
}
.linkInputs {

}
.AddInputs {
  float:right;
}
.ReactModal__Overlay {
  background-color:rgba(0,0,0,0.7) !important;
}
.ReactModal__Content {
  background-color:#444 !important;
  .closebutton {
    right:20px;
    top:20px;
  }
  .modalIframeContainer {
    iframe {
      margin:0 auto;
      display:block;
    }
  }
}